.success {
    width: 25%;
    color: green;
    font-size: 18px;
    border:  3px solid green;
    background-color: #f1f1f1;
    padding: 3px;
    margin: 10px;
    border-radius: 5px;
    
}

.error {
    width: 25%;
    color: red;
    font-size: 18px;
    border:  3px solid red;
    background-color: #f1f1f1;
    padding: 3px;
    margin: 10px;
    border-radius: 5px;  
}